* {
  padding: 0 0;
  margin: 0 0;
  -webkit-text-size-adjust: none;
}
body {
  background-color: #0e0e0e;
  font-family: Apple SD Gothic Neo, Noto Sans KR, Noto Sans, Roboto, sans-serif;
  color: #000;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
  -moz-text-size-adjust: none;
  -o-text-size-adjust: none;
}
input {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
}
img {
  width: 100%;
  vertical-align: top;
}

body.no-scroll {
  touch-action: none;
  -webkit-overflow-scrolling: none;
  overflow: hidden;
  overscroll-behavior: none;
}

.btn {
  display: inline-block;
  text-align: center;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  white-space: nowrap;
  font-size: 14px;
  background: transparent;
  border: 1px solid transparent;
  line-height: 40px;
  font-weight: 700;
  width: 100%;
}

.btn-primary {
  background-color: #a839ff;
  color: #fff;
  border-color: #a839ff;
}

.input-group {
  margin-bottom: 12px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  .certification-area {
    position: relative;
    display: inherit;
    width: 100%;
    .time {
      color: #ff4f5d;
      position: absolute;
      top: 17px;
      right: 12px;
      font-size: 12px;
    }
  }
  .input-box {
    line-height: 43px;
    border: 1px solid #fff;
    padding: 0 30px 0px 12px;
    color: #c137fe;
    width: 100%;
    font-size: 14px;
  }
  .input-box:disabled {
    background: rgb(94, 94, 94);
    border-color: rgb(94, 94, 94);
    color: #ababab;
    opacity: 1;
  }
  .input-box:disabled::-webkit-input-placeholder {
    color: #ababab;
    opacity: 1;
  }
  .input-box:disabled::-moz-placeholder {
    color: #ababab;
    opacity: 1;
  }
  .input-box:disabled::-ms-input-placeholder {
    color: #ababab;
    opacity: 1;
  }
  .input-box:disabled::placeholder {
    color: #ababab;
    opacity: 1;
  }
  .input-box:focus {
    border: 1px solid #c137fe;
    outline: none;
  }
  .input-box::-webkit-input-placeholder {
    color: #a2a2a2;
    opacity: 1;
  }
  .input-box::-moz-placeholder {
    color: #a2a2a2;
    opacity: 1;
  }
  .input-box::-ms-input-placeholder {
    color: #a2a2a2;
    opacity: 1;
  }
  .input-box::placeholder {
    color: #a2a2a2;
    opacity: 1;
  }
  .input-box::-webkit-input-placeholder {
    color: #a2a2a2;
  }
  .input-box:-ms-input-placeholder {
    color: #a2a2a2;
  }
  .input-box:-ms-input-placeholder {
    color: #a2a2a2;
  }

  .input-box::-webkit-input-placeholder {
    color: #a2a2a2;
  }
  .input-btn {
    border: 1px solid #c137fe;
    background-color: #c137fe;
    color: #fff;
    width: 110px;
    line-height: 44px;
    font-size: 14px;
    font-weight: bold;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    &.disabled {
      border: rgba(193, 55, 254, 0.5);
      background-color: rgba(193, 55, 254, 0.5);
      color: rgba(255, 255, 255, 0.5);
      -webkit-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
      -o-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
      transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
    }
  }
}

.modal-popup {
  visibility: visible;
  position: fixed;
  opacity: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1000;
  .modal-container {
    position: fixed;
    left: 50%;
    right: 50%;
    top: 50%;
    bottom: auto;
    z-index: 99;
    width: 100%;
    max-width: 450px;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  .modal-content {
    margin: 0 20px;
    background: #fff;
    font-size: 14px;
    padding: 32px 24px 24px;
  }
  .btn-group {
    padding-top: 24px;
  }
  &.off {
    visibility: hidden;
    opacity: 0;
  }
  &.small-type {
    .modal-container {
      width: 320px;
      .info-text {
        text-align: center;
        font-size: 14px;
        margin-bottom: 4px;
      }
    }
  }
}

.open-event-wrap {
  margin: 0 auto;
  max-width: 450px;

  .event-form-section {
    padding-top: 56px;
    padding-bottom: 56px;
    padding-left: 24px;
    padding-right: 24px;
    background-color: #0e0e0e;
    .title {
      margin-bottom: 24px;
    }
    .btn-event {
      border: none;
      margin-top: 24px;
      background-color: #c137fe;
      height: 46px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      width: 100%;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
      -webkit-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
      -o-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
      transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);

      .icon {
        width: 22px;
        margin-left: 8px;
        -webkit-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
        -o-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
        transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
      }
      &.disabled {
        background-color: rgba(193, 55, 254, 0.5);
        color: rgba(255, 255, 255, 0.5);
        -webkit-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
        -o-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
        transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
        .icon {
          opacity: 0.5;
          -webkit-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
          -o-transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
          transition: all 0.3s cubic-bezier(0, 0.21, 0.03, 1.01);
        }
      }
    }
  }

  .terms-checkbox-group {
    padding: 20px 12px 0px;
    .terms-checkbox {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
      margin-bottom: 8px;
      .btn-more {
        border: none;
        background-color: #505050;
        img {
          width: 60px;
        }
      }
    }
    .checkbox {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
      input[type='checkbox'] + .checkbox-label::before {
        content: '';
        position: absolute;
        display: inline-block;
        background: url(/asset/img/btn-checkbox.png) no-repeat center center / contain;
        width: 13px;
        height: 13px;
        margin-right: 8px;
      }

      input[type='checkbox']:checked + .checkbox-label::before {
        background: url(/asset/img/btn-checkbox-on.png) no-repeat center center / contain;
      }
      input[type='checkbox'] {
        display: none;
      }
      .checkbox-label {
        position: relative;
        .text {
          margin-left: 20px;
          display: block;
          color: #fff;
          font-size: 11px;
        }
      }
    }
  }
  .terms-group {
    .terms-title {
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 12px;
    }
    .terms-text {
      color: rgba(0, 0, 0, 0.6);
      margin-bottom: 8px;
      font-size: 12px;
    }
    .terms-list {
      li {
        position: relative;
        list-style: none;
        padding-left: 10px;
        margin-bottom: 4px;
        color: rgba(0, 0, 0, 0.6);
        font-size: 12px;
      }
      li:after {
        content: '';
        display: block;
        width: 3px;
        height: 3px;
        border-radius: 5px;
        background-color: rgba(0, 0, 0, 0.6);
        position: absolute;
        top: 5px;
        left: 0px;
      }
    }
  }
}
